#Shop{
    width: 100%;
    height: 100%;
    padding: 0.75rem 1rem 10rem 1rem;
}
#Shop-Nav{
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#Shop-Nav>div:first-child>a{
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0.125rem 0.25rem;
    color: var(--color-grey-dark);
    -webkit-transition: 1.25s;
    transition: 1.25s;
}
#Shop-Nav>div:first-child>a.Active{
    color: var(--color-gold-dark);
}
#Shop-Nav>div:first-child>a>span{
    font-size: var(--font-size-small);
    font-weight: 600;
}
#Shop-Nav>div:first-child>a:first-child{
    margin-right: 1rem;
}
#Shop-Nav>div:last-child{
    padding: 0.125rem 0.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    color: var(--color-grey-dark);
    -webkit-transition: 1.25s;
    transition: 1.25s;
}
#Shop-Nav>div:last-child:hover{
    color: var(--color-gold-dark);
    cursor: pointer;
}
#Shop-Nav>div:last-child>span{
    font-size: var(--font-size-small);
    font-weight: 600;
}
#Shop-Content{
    height: 90%;
}
#Shop-Seachbox{
    color: var(--color-black-dark);
    font-weight: 600;
}
#Shop-Seachbox input{
    display: block;
    width: 100%;
    border: 0;
    border-radius: 0;
    box-shadow: 0 0 0 0 transparent;
    border-bottom: 1px solid var(--color-black-dark);
    text-align: center;
    letter-spacing: 0.1rem;
}
#Shop-Seachbox input::-webkit-input-placeholder{
    color: var(--color-black-light);
    font-weight: 300;
}
#Shop-Seachbox input::-ms-input-placeholder{
    color: var(--color-black-light);
    font-weight: 300;
}
#Shop-Seachbox input::placeholder{
    color: var(--color-black-light);
    font-weight: 300;
}
#Shop-Seachbox>div.Filter{
    margin: 2.5rem 0 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#Shop-Seachbox>div.Filter>div{
    flex-grow: 1;
}
#Shop-Seachbox>div.Filter>div:first-child{
    padding-right: 0.5rem;
}
#Shop-Seachbox>div.Filter>div:last-child{
    padding-left: 0.5rem;
}   
div.Dropdown{
    color: var(--color-black-light);
    position: relative;
    font-size: var(--font-size-small);
}
div.Dropdown>p.Title{
    text-align: left;
}
div.Dropdown>div.Select{
    padding: 0.25rem 0.75rem;
    border: 1px solid var(--color-grey-dark);
    border-radius: 0.5rem;
    position: relative;
    text-align: left;
}
div.Dropdown>div.Select:hover{
    cursor: pointer;
}
div.Dropdown>div.Select>i{
    position: absolute;
    right: 1.25rem;
    top: 0.25rem;
    color: var(--color-grey-dark);
}
div.Dropdown div.Dropdown-Item{
    text-align: left
}
div.Dropdown div.Dropdown-Item>p{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
div.Dropdown div.Dropdown-Item>p>img{
    height: 1.5rem;
    margin-right: 0.5rem;
}
div.Dropdown-Content{
    display: none;
    z-index: -1;
    position: absolute;
    width: 100%;
    -webkit-transform: translateY(0.5rem);
            transform: translateY(0.5rem);
    background-color: var(--color-white-normal);
    border-radius: 0.5rem;
}
div.Dropdown-Content.Active{
    display: block;
    z-index: 3;
}
div.Dropdown>div.Dropdown-Content>div.Dropdown-Item{
    padding: 0.25rem 0.75rem;
}
div.Dropdown>div.Dropdown-Content>div.Dropdown-Item:hover{
    cursor: pointer;
    background-color: var(--color-gold-dark);
    color: var(--color-white-light);
}
div.Dropdown>div.Dropdown-Content>div.Dropdown-Item:first-child{
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
}
div.Dropdown>div.Dropdown-Content>div.Dropdown-Item:last-child{
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
}
